body {
  font-family: 'Roboto';
  background-color: rgb(251, 248, 248)
}

.App {
  position: relative;
  height: 500px;

}















.accordion {
  padding: 9px;

  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  margin: 8px;


  position: relative;
  top: 0;
  left: 50%;
  width: 40vw !important;
  transform: translateX(-52%);

  animation: fallDownAccordion 1.5s ease-in-out forwards;
  opacity: 0.03;
}

button {
  position: relative;
  top: 0;
  left: 50%;
  padding: 9px;
  transform: translateX(-50%);
  animation: fallDownButton 1.5s ease-in-out forwards;
  animation-delay: 0.7s;

}

.description {
  position: relative;
  font-family: 'Dosis';
}

.accordion:nth-child(even) {
  animation-delay: 0.5s;
}

.image {
  height: 200px;
  width: 150px;
  position: relative;
  left: 0%;
  animation: leftToRight 1s ease-in-out forwards 0.3s;
  animation-delay: 0.3s;



}

@keyframes leftToRight {

  to {
    opacity: 1;
    left: 50%;
    transform: translateX(-50%);

  }
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.accordion:nth-child(odd) {
  animation-delay: 0.2s;
}

@keyframes fallDownAccordion {
  to {
    top: 20%;
    width: 440px;
    opacity: 1
  }
}

@keyframes fallDownButton {
  to {
    top: 20%;
    opacity: 1
  }
}

span {
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .accordion {
    width: 80vw !important;
  }

}